import React from "react";
import {createGlobalStyle} from "styled-components";
import EventCell from "./EventCell";
const CalendarStyles = createGlobalStyle`
    #calendar{
        width: 85%;
        min-width: 810px;
        display: flex;
        flex-direction: column;
        align-items: stretch;
        padding: 15px;
        box-sizing: border-box;
    }
    #calendar > div{
        height: 100px;
        width: 100%;
        display: flex;
        flex-direction: row-reverse;
        border-bottom: 3px solid white;
    }
    #calendar > div:last-child{
        border: none;
    }
    .infocell{
        background-color: #b9deff;
        min-width: 120px;
        width: 120px;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        float:right;
        border-left: 2px solid white;
    }
    #calHeader{
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-between;
        align-items: center;
    }
    #calHeader{
        background-color: #b9deff;
    }
    .hourcell{
        box-sizing: border-box;
        border-left: 1px dashed darkgray;
        display:flex;
        justify-content: flex-start;
        align-items: center;
        height:70%;
        padding-left: 2px;
    }
    .eventContainer{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: row-reverse;
        position: relative;
    }
    div.eventCell *{
        pointer-events: none;
    }
    div.eventCellName{
        color: white;
        margin: 10px 0px;
        padding: 0px 10px;
        text-shadow: 0px 0px 2px black;
        text-align: initial;
        height: 2.6em;
        text-overflow: ellipsis;
        overflow: hidden;
        line-height: 1.2em;
        user-select: none;
    }
    div.eventCellTime{
        position:absolute;
        left: 0px;
        bottom: 0px;
        margin: 10px 10px;
        text-shadow: 0px 0px 3px white;
        text-align: initial;
        height: 2.6em;
        line-height: 1em;
        display: flex;
        align-items: end;
        user-select: none;
    }
    div.eventCellDesc{
        color: white;
        border-radius: 15px;
        background-color: rgba(0,0,0,0.7);
        backdrop-filter: blur(10px);
        white-space: nowrap;
        max-width: 200px;
        text-overflow: ellipsis;
        overflow: hidden;
        z-index: 100;
        padding: 10px;
        position: absolute;
        top: -50px;
        opacity: 0%;
        transition: opacity 0.5s ease 0.5s;
        user-select: none;
    }
`
function Calendar(props){
    let allevents = JSON.parse(localStorage.getItem("WeeklyProgrammerData"))
    let eventKey = 0
    return(<div id="calendar">
        <CalendarStyles/>
        <div id="calHeader">
            <div className="infocell">

            </div>
            <div className="hourcell">
                <p>10:00</p>
            </div>
            <div className="hourcell">
                <p>12:00</p>
            </div>
            <div className="hourcell">
                <p>14:00</p>
            </div>
            <div className="hourcell">
                <p>16:00</p>
            </div>
            <div className="hourcell">
                <p>18:00</p>
            </div>
            <div className="hourcell">
                <p>20:00</p>
            </div>
            <div className="hourcell">
                <p>22:00</p>
            </div>
            <div className="hourcell">
                <p>24:00</p>
            </div>

        </div>
        <div className="calDay">
            <div className="infocell">
                Saturday
            </div>
            <div className="eventContainer">
                {allevents.filter((e)=>{
                    return parseInt(e.day)===0
                }).map((e)=>{
                    return <EventCell key={eventKey++} setEventDesc={props.setEventDesc} day={e.day} name={e.name} desc={e.desc} startHour={e.startHour} startMin={e.startMin} endHour={e.endHour} endMin={e.endMin} color={e.color} />
                })}
            </div>
        </div>
        <div className="calDay">
            <div className="infocell">
                Sunday
            </div>
            <div className="eventContainer">
                {allevents.filter((e)=>{
                    return parseInt(e.day)===1
                }).map((e)=>{
                    return <EventCell key={eventKey++} setEventDesc={props.setEventDesc} day={e.day} name={e.name} desc={e.desc} startHour={e.startHour} startMin={e.startMin} endHour={e.endHour} endMin={e.endMin} color={e.color} />
                })}
            </div>
        </div>
        <div className="calDay">
            <div className="infocell">
                Monday
            </div>
            <div className="eventContainer">
                {allevents.filter((e)=>{
                    return parseInt(e.day)===2
                }).map((e)=>{
                    return <EventCell key={eventKey++} setEventDesc={props.setEventDesc} day={e.day} name={e.name} desc={e.desc} startHour={e.startHour} startMin={e.startMin} endHour={e.endHour} endMin={e.endMin} color={e.color} />
                })}
            </div>
        </div>
        <div className="calDay">
            <div className="infocell">
                Tuesday
            </div>
            <div className="eventContainer">
                {allevents.filter((e)=>{
                    return parseInt(e.day)===3
                }).map((e)=>{
                    return <EventCell key={eventKey++} setEventDesc={props.setEventDesc} day={e.day} name={e.name} desc={e.desc} startHour={e.startHour} startMin={e.startMin} endHour={e.endHour} endMin={e.endMin} color={e.color} />
                })}
            </div>
        </div>
        <div className="calDay">
            <div className="infocell">
                Wednesday
            </div>
            <div className="eventContainer">
                {allevents.filter((e)=>{
                    return parseInt(e.day)===4
                }).map((e)=>{
                    return <EventCell key={eventKey++} setEventDesc={props.setEventDesc} day={e.day} name={e.name} desc={e.desc} startHour={e.startHour} startMin={e.startMin} endHour={e.endHour} endMin={e.endMin} color={e.color} />
                })}
            </div>
        </div>
        <div className="calDay">
            <div className="infocell">
                Thursday
            </div>
            <div className="eventContainer">
                {allevents.filter((e)=>{
                    return parseInt(e.day)===5
                }).map((e)=>{
                    return <EventCell key={eventKey++} setEventDesc={props.setEventDesc} day={e.day} name={e.name} desc={e.desc} startHour={e.startHour} startMin={e.startMin} endHour={e.endHour} endMin={e.endMin} color={e.color} />
                })}
            </div>
        </div>
        <div className="calDay">
            <div className="infocell">
                Friday
            </div>
            <div className="eventContainer">
                {allevents.filter((e)=>{
                    return parseInt(e.day)===6
                }).map((e)=>{
                    return <EventCell key={eventKey++} setEventDesc={props.setEventDesc} day={e.day} name={e.name} desc={e.desc} startHour={e.startHour} startMin={e.startMin} endHour={e.endHour} endMin={e.endMin} color={e.color} />
                })}
            </div>
        </div>
    </div>)
}
export default Calendar