import React from "react";
function EventDescription({day,startHour,startMin,endHour,endMin,name,desc}){
    const days = ["Saturday","Sunday","Monday","Tuesday","Wednesday","Thursday","Friday"]
    return(<div className="eventDescBox">
        <h2 className="eventDescName">{name}</h2>
        <p className="eventDescDate">{days[day]} , {startHour}:{parseInt(startMin).toLocaleString("fullwide",{minimumIntegerDigits:2})} - {endHour}:{parseInt(endMin).toLocaleString("fullwide",{minimumIntegerDigits:2})}</p>
        <p className="eventDescText">{desc}</p>
    </div>)
}
export default EventDescription