import React, {useState} from "react";
import {createGlobalStyle} from "styled-components";
const ImportFormStyles = createGlobalStyle`
    .importEventForm{
        width: 60vw;
        background-color: white;
        border-radius: 10px;
        color: #46a8ff;
        box-shadow: 0px 0px 5px #b9deff;
        margin-top: 15px;
        overflow: hidden;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .importEventForm #importFormHead{
        margin: 0 0 15px 0;
        background-color: #46a8ff;
        color: white;
        padding: 5px 0;
        width: 100%;
    }
    .importEventForm #importFormHead p{
        padding: 0;
        margin: 0;
    }
    .importEventForm #importFormBtn{
        border: none;
        outline: none;
        color: white;
        background-color: #7dc2ff;
        border-radius: 5px;
        padding: 5px;
        margin: 5px;
        width: min-content;
        cursor: pointer;
        width: 70px;
        height: 50px;
    }
    .importEventForm #importFormBtn:hover{
        background-color: #46a8ff;
    }
    .importEventForm #importFormCancel{
        border: none;
        outline: none;
        color: white;
        background-color: darkgray;
        border-radius: 5px;
        padding: 5px;
        margin: 5px;
        width: min-content;
        cursor: pointer;
        width: 70px;
        height: 50px;
    }
    .importEventForm #importFormCancel:hover{
        background-color: gray;
    }
    .importEventForm #importFormText{
        resize: vertical;
        width: 80%;
        background-color: lightgray;
        border: 2px solid #b9deff;
        font-family: monospace;
        outline: none;
    }
    .importEventForm #importFormText:focus{
        border-color: #1892ff;
    }
    #importFormBtns{
        margin: 10px 0;
        display: flex;
    }
`
function ImportEventForm({setImporting}){
    const [code,setCode] = useState("")
    const codeHandler = (e)=>{
        setCode(e.target.value)
    }
    const importValidator = () =>{
        let valid = false;
        if(code[0]==="{" && code[code.length-1]==="}"){
            let event = JSON.parse(code)
            if(event.name !== undefined && event.name !== "" && event.desc !== undefined && event.color !== undefined && event.color !== "" && event.day !== undefined && event.day !== "" && event.startHour !== undefined && event.startHour !== "" && event.startMin !== undefined && event.startMin !== "" && event.endHour !== undefined && event.endHour !== "" && event.endMin !== undefined && event.endMin !== ""){
                if(parseInt(event.day)>=0 && parseInt(event.day)<=6){
                    if(7<parseInt(event.endHour) && 24>parseInt(event.endHour) && 7<parseInt(event.startHour) && 24>parseInt(event.startHour) && 0<=parseInt(event.endMin) && 60>parseInt(event.endMin) && 0<=parseInt(event.startMin) && 60>parseInt(event.startMin)){
                        if(!(parseInt(event.endHour)<parseInt(event.startHour) || (parseInt(event.endHour)===parseInt(event.startHour) && parseInt(event.endMin)<=parseInt(event.startMin)))){
                            valid = true
                            let allevents = JSON.parse(localStorage.getItem("WeeklyProgrammerData"))
                            allevents.push({name:event.name,
                                day:event.day,
                                startHour:event.startHour,
                                startMin:event.startMin,
                                endHour:event.endHour,
                                endMin:event.endMin,
                                desc:event.desc,
                                color:event.color})
                            localStorage.setItem("WeeklyProgrammerData",JSON.stringify(allevents))
                            setImporting(false)
                        }
                    }
                }
            }
        }
        if(!valid){
            alert("Invalid Event code.")
        }
    }
    return(<>
        <ImportFormStyles/>
        <div className="importEventForm">
        <div id="importFormHead"><p>Enter the Event code here</p></div>
        <textarea id="importFormText" placeholder="Event code here" value={code} onChange={codeHandler}>

        </textarea>
            <div id="importFormBtns">
                <button id="importFormCancel" onClick={()=>{setImporting(false)}}>Cancel</button>
                <button id="importFormBtn" onClick={importValidator}>Import</button>
            </div>
    </div></>)
}
export default ImportEventForm