import styled from "styled-components";
export const EventCellStyled = styled.div`
    height: 100%;
    background-color: ${props => props.color};
    color: ${props => ((parseInt(props.color.substring(1,3),16)*299 + parseInt(props.color.substring(3,5),16)*587 + parseInt(props.color.substring(5,7),16)*114)/1000 >= 128) ? "gray" : "lightgray"};
    position: absolute;
    right: ${props => (props.starthour-8)*6.25 + props.startmin*(6.25/60)}%;
    width: ${props => (props.endhour-props.starthour)*6.25 + (props.endmin-props.startmin)*(6.25/60)}%;
    &:hover{
        background-image: linear-gradient(to bottom,${props => props.color} 85%, rgba(0,0,0,0.5) 100%);
    }
`