import React, {useState} from "react";
import {createGlobalStyle} from "styled-components";
const BackupSectionStyles = createGlobalStyle`
    #backupSection{
        width: 85%;
        padding: 15px;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
    }
    #backupSection h1{
        margin: 10px 15px 0 15px;
    }
    #backupSection > div{
        box-shadow: 0px 0px 5px #b9deff;
        width: 60vw;
        box-sizing: border-box;
        margin-top: 15px;
        background-color: white;
        border-radius: 15px;
        color: #46a8ff;
    }
    #exportSection{
        text-align: center;
    }
    #exportSection > div{
        border-radius: 15px;
        padding: 15px;
        box-sizing: border-box;
        margin: 0 auto;
    }
    #backupSection textarea{
        width: 90%;
        background-color: lightgray;
        border: 2px solid #b9deff;
        font-family: monospace;
        outline: none;
        resize: none;
        height: 20em;
        overflow: hidden;
    }
    #backupSection textarea:focus{
        border-color: #1892ff;
    }
    #importSection textarea{
        overflow-y: scroll;
    }
    #backupSection button{
        border: none;
        outline: none;
        color: white;
        background-color: #7dc2ff;
        border-radius: 5px;
        padding: 5px 10px;
        margin: 5px;
        cursor: pointer;
        height: 50px;
    }
    #backupSection button:hover{
        background-color: #46a8ff;
    }
    #warningText{
        color: red;
    }
`


function BackupSection(){
    const [code,setCode] = useState("")
    const codeHandler = (e)=>{
        setCode(e.target.value)
    }
    const copyEvent = (e)=>{
        navigator.clipboard.writeText(localStorage.getItem("WeeklyProgrammerData"));
        e.target.innerText = "Copied!"
    }
    const downloadCode = ()=>{
        let FileSaver = require('file-saver');
        let blob = new Blob([localStorage.getItem("WeeklyProgrammerData")], {type: "text/plain;charset=utf-8"});
        FileSaver.saveAs(blob, "ExportedCalendar.txt",{ autoBom: true });
    }
    const importValidator = ()=>{
        try{
            let allvalid = true;
            let newCal = []
            if(code[0]==="[" && code[code.length-1]==="]"){
                for(let calEvent of JSON.parse(code)){
                    calEvent = JSON.stringify(calEvent)
                    let valid = false
                    if(calEvent[0]==="{" && calEvent[calEvent.length-1]==="}"){
                        let event = JSON.parse(calEvent)
                        if(event.name !== undefined && event.name !== "" && event.desc !== undefined && event.color !== undefined && event.color !== "" && event.day !== undefined && event.day !== "" && event.startHour !== undefined && event.startHour !== "" && event.startMin !== undefined && event.startMin !== "" && event.endHour !== undefined && event.endHour !== "" && event.endMin !== undefined && event.endMin !== ""){
                            if(parseInt(event.day)>=0 && parseInt(event.day)<=6){
                                if(7<parseInt(event.endHour) && 24>parseInt(event.endHour) && 7<parseInt(event.startHour) && 24>parseInt(event.startHour) && 0<=parseInt(event.endMin) && 60>parseInt(event.endMin) && 0<=parseInt(event.startMin) && 60>parseInt(event.startMin)){
                                    if(!(parseInt(event.endHour)<parseInt(event.startHour) || (parseInt(event.endHour)===parseInt(event.startHour) && parseInt(event.endMin)<=parseInt(event.startMin)))){
                                        valid = true
                                        newCal.push({name:event.name,
                                            day:event.day,
                                            startHour:event.startHour,
                                            startMin:event.startMin,
                                            endHour:event.endHour,
                                            endMin:event.endMin,
                                            desc:event.desc,
                                            color:event.color})

                                    }
                                }
                            }
                        }
                    }
                    if(!valid){
                        allvalid = false;
                        break
                    }
                }
            }else{
                allvalid = false
            }
            if(allvalid){
                localStorage.setItem("WeeklyProgrammerData",JSON.stringify(newCal))
                alert("Calendar imported successfully.")
            }else{
                alert("Invalid Event code.")
            }
        }catch (error){
            alert("An error occurred while importing.")
        }

    }
    return(<>
        <BackupSectionStyles/>
        <div id="backupSection">
            <div id="exportSection">
                <h1>Export your current Calendar</h1>
                <div id="exportSectionCode">
                        <p>Use the code below to export your calendar to other devices</p>
                        <textarea readOnly={true} value={localStorage.getItem("WeeklyProgrammerData")}></textarea><br/>
                        <button onClick={copyEvent}>Click to copy</button>
                        <button onClick={downloadCode}>Download text file</button>
                </div>
            </div>
            <div id="importSection">
                <h1>Import a previously made Calendar</h1>
                <p id="warningText">WARNING: This will overwrite your current Calendar!</p>
                <p>Enter your Calendar code here</p>
                <textarea value={code} onChange={codeHandler} placeholder="Calendar Code here"></textarea><br/>
                <button onClick={importValidator}>Import Calendar</button>
            </div>
        </div></>)
}
export default BackupSection