import React, {useState} from "react";
import EditEventForm from "./EditEventForm";
function EditSectionEvent({day,startHour,startMin,endHour,endMin,name,desc,color,setTriggerkey}){
    const days = ["Saturday","Sunday","Monday","Tuesday","Wednesday","Thursday","Friday"]
    const [editing,setEditing] = useState(false)
    const [exporting,setExporting] = useState(false)
    const editEvent = ()=>{
        setEditing(true)
    }
    const copyEvent = (e)=>{
        navigator.clipboard.writeText(JSON.stringify({day,startHour,startMin,endHour,endMin,name,desc,color}));
        e.target.innerText = "Copied!"
    }
    return(<>{(editing) ? <EditEventForm action="edit" setAction={setEditing} setTrigger={setTriggerkey} event={{day,startHour,startMin,endHour,endMin,name,desc,color}}/> :
        <div className="eventDescBox" style={{boxShadow:`0px 0px 5px ${color}`}}>
        <h2 className="eventDescName">{name}</h2>
        <div className="eventEditBtn" title="Edit this event" onClick={editEvent}>
            <svg xmlns="http://www.w3.org/2000/svg" width="25" height="25" fill="currentColor"
                 className="bi bi-pencil-fill" viewBox="0 0 16 16">
                <path
                    d="M12.854.146a.5.5 0 0 0-.707 0L10.5 1.793 14.207 5.5l1.647-1.646a.5.5 0 0 0 0-.708l-3-3zm.646 6.061L9.793 2.5 3.293 9H3.5a.5.5 0 0 1 .5.5v.5h.5a.5.5 0 0 1 .5.5v.5h.5a.5.5 0 0 1 .5.5v.5h.5a.5.5 0 0 1 .5.5v.207l6.5-6.5zm-7.468 7.468A.5.5 0 0 1 6 13.5V13h-.5a.5.5 0 0 1-.5-.5V12h-.5a.5.5 0 0 1-.5-.5V11h-.5a.5.5 0 0 1-.5-.5V10h-.5a.499.499 0 0 1-.175-.032l-.179.178a.5.5 0 0 0-.11.168l-2 5a.5.5 0 0 0 .65.65l5-2a.5.5 0 0 0 .168-.11l.178-.178z"/>
            </svg>
        </div>
        <div className="eventExportBtn" title="Export this event" onClick={()=>{setExporting(!exporting)}}>
            <svg xmlns="http://www.w3.org/2000/svg" width="25" height="25" fill="currentColor"
                 className="bi bi-box-arrow-up-right" viewBox="0 0 16 16">
                <path fillRule="evenodd"
                      d="M8.636 3.5a.5.5 0 0 0-.5-.5H1.5A1.5 1.5 0 0 0 0 4.5v10A1.5 1.5 0 0 0 1.5 16h10a1.5 1.5 0 0 0 1.5-1.5V7.864a.5.5 0 0 0-1 0V14.5a.5.5 0 0 1-.5.5h-10a.5.5 0 0 1-.5-.5v-10a.5.5 0 0 1 .5-.5h6.636a.5.5 0 0 0 .5-.5z"/>
                <path fillRule="evenodd"
                      d="M16 .5a.5.5 0 0 0-.5-.5h-5a.5.5 0 0 0 0 1h3.793L6.146 9.146a.5.5 0 1 0 .708.708L15 1.707V5.5a.5.5 0 0 0 1 0v-5z"/>
            </svg>
        </div>
        <p className="eventDescDate">{days[day]} , {startHour}:{parseInt(startMin).toLocaleString("fullwide",{minimumIntegerDigits:2})} - {endHour}:{parseInt(endMin).toLocaleString("fullwide",{minimumIntegerDigits:2})}</p>
        <p className="eventDescText">{desc}</p>
            {(exporting && !editing) ? <div className="eventExportBox">
                <p>Use the code below to import this Event on another place.</p>
                <input type="text" readOnly={true} value={JSON.stringify({day,startHour,startMin,endHour,endMin,name,desc,color})}/>
                <button className="eventExportCopyBtn" onClick={copyEvent}>Copy</button>
            </div> : null}
    </div>
    }

    </>)
}
export default EditSectionEvent