import React, {useEffect, useState} from "react";
import {createGlobalStyle} from "styled-components";
const EditEventFormStyles = createGlobalStyle`
    .editEventForm{
        width: 60vw;
        background-color: white;
        border-radius: 10px;
        color: #46a8ff;
        box-shadow: 0px 0px 5px #b9deff;
        margin-top: 15px;
        overflow: hidden;
    }
    #editEventFormName{
        width: 100%;
        max-width: 30ch;
        height: 25px;
        border: none;
        outline: none;
        border-bottom: 4px solid #b9deff;
        border-radius: 4px;
        background-color: #f7f7f7;
    }
    #editEventFormName:focus{
        border-bottom: 4px solid #46a8ff;
    }
    #eventFormNameBox{
        width: 100%;
        padding: 10px;
        box-sizing: border-box;
        float: left;
        text-align: left;
    }
    #eventFormNameBox p{
        margin:0;
        padding: 0;
    }
    #editEventFormDesc{
        width: 100%;
        max-width: 60ch;
        height: 75px;
        border: none;
        outline: none;
        border-bottom: 4px solid #b9deff;
        border-radius: 4px;
        background-color: #f7f7f7;
        font-family: sans-serif;
        resize: none;
    }
    #editEventFormDesc:focus{
        border-bottom: 4px solid #46a8ff;
    }
    #eventFormDescBox{
        width: 100%;
        padding: 10px;
        box-sizing: border-box;
        float: left;
        clear: left;
        text-align:left;
    }
    #eventFormDescBox p{
        margin:0;
        padding: 0;
    }
    #editEventFormDay{
        outline:none;
        border-color: #b9deff;
    }
    #editEventFormDay:focus{
        border-color: #46a8ff;
    }
    #eventFormDayBox{
        margin: 10px;
    }
    .timeBox{
        margin: 10px;
        min-width: max-content;
    }
    .timeBox span{
        font-size: 30px;
        margin: 0 5px;
    }
    .timeBox input{
        outline: none;
        border: 2px solid #b9deff;
        font-size: 30px;
        width: 50px;
    }
    .timeBox input:focus{
        border-color: #46a8ff;
    }
    #eventFormDayTime{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    #eventFormDayTime > div{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
    }
    #editEventFormColor{
        margin: 5px;
        border-color: #b9deff;
    }
    #editEventFormColor:focus{
        border-color: #46a8ff;
    }
    #editEventFormCancel{
        margin: 10px;
        width: 70px;
        height: 50px;
        background-color: darkgray;
        color: white;
        border: none;
        border-radius: 5px;
    }
    #editEventFormCancel:hover{
        background-color: gray;
    }
    #editEventFormSave{
        margin: 10px;
        width: 70px;
        height: 50px;
        background-color: #7dc2ff;
        color: white;
        border: none;
        border-radius: 5px;
    }
    #editEventFormSave:hover{
        background-color: #46a8ff;
    }
    #editEventFormRemove{
        margin: 10px;
        width: 70px;
        height: 50px;
        background-color: #ff4d4d;
        color: white;
        border: none;
        border-radius: 5px;
    }
    #editEventFormRemove:hover{
        background-color: #ff1f1f;
    }
    .editEventForm #eventFormHead{
        margin: 0 0 15px 0;
        background-color: #46a8ff;
        color: white;
        padding: 5px 0;
        width: 100%;
    }
    .editEventForm #eventFormHead p{
        margin: 0;
        padding: 0;
    }
`
function EditEventForm({action,setAction,setTrigger,event}) {
    const [name,setName] = useState("")
    const [day,setDay] = useState(0)
    const [startHour,setStarthour] = useState("8")
    const [startMin,setStartmin] = useState("0")
    const [endHour,setEndhour] = useState("9")
    const [endMin,setEndmin] = useState("0")
    const [desc,setDesc] = useState("")
    const [color,setColor] = useState("#ffffff")
    useEffect(()=>{
        if(action==="edit"){
            setName(event.name)
            setDay(event.day)
            setStarthour(event.startHour)
            setStartmin(event.startMin)
            setEndhour(event.endHour)
            setEndmin(event.endMin)
            setDesc(event.desc)
            setColor(event.color)
        }
    },[event])
    const changeHandler = (e,setVal) =>{
        setVal(e.target.value)
    }
    const submitHandler = (e)=>{
        e.preventDefault()
        if(parseInt(endHour)<parseInt(startHour) || (parseInt(endHour)===parseInt(startHour) && parseInt(endMin)<=parseInt(startMin))){
            document.querySelector("#editEventFormEndHour").style.borderColor="red"
            document.querySelector("#editEventFormEndMin").style.borderColor="red"
            alert("End time should be after Start time.")
        }else{
            let allevents = JSON.parse(localStorage.getItem("WeeklyProgrammerData"))
            allevents.push({name,
                day,
                startHour,
                startMin,
                endHour,
                endMin,
                desc,
                color})
            if (action==="edit"){
                for(let e of allevents){
                    if(e.name===event.name && e.day===event.day && e.startHour===event.startHour && e.startMin===event.startMin && e.endHour===event.endHour && e.endMin===event.endMin && e.desc===event.desc && e.color===event.color){
                        allevents.splice(allevents.indexOf(e),1);
                        break;
                    }
                }
            }
            localStorage.setItem("WeeklyProgrammerData",JSON.stringify(allevents))
            setAction(false)
            setTrigger(prevState => prevState+1)
        }
    }
    const removeEvent = ()=>{
        let allevents = JSON.parse(localStorage.getItem("WeeklyProgrammerData"))
        if (action==="edit"){
            for(let e of allevents){
                if(e.name===event.name && e.day===event.day && e.startHour===event.startHour && e.startMin===event.startMin && e.endHour===event.endHour && e.endMin===event.endMin && e.desc===event.desc && e.color===event.color){
                    allevents.splice(allevents.indexOf(e),1);
                    break;
                }
            }
        }
        localStorage.setItem("WeeklyProgrammerData",JSON.stringify(allevents))
        setAction(false)
        setTrigger(prevState => prevState+1)
    }
    return(<div className="editEventForm">
        <EditEventFormStyles/>
        <div id="eventFormHead"><p>{(action==="edit") ? `Editing ${event.name} Event` : "Creating new Event"}</p></div>
        <form onSubmit={(e)=>{submitHandler(e)}}>
            <div id="eventFormNameBox">
                <p>Name</p><input id="editEventFormName" required={true} placeholder="Enter Name" type="text" value={name} onChange={(event)=>{changeHandler(event,setName)}}/>
            </div>
            <div id="eventFormDescBox">
                <p>Description</p><textarea id="editEventFormDesc" placeholder="Enter Description" value={desc} onChange={(event)=>{changeHandler(event,setDesc)}}/>
            </div>
            <div id="eventFormDayTime">
                <div>
                <div id="eventFormDayBox">
                    <p>Day</p>
                    <select id="editEventFormDay" value={day} onChange={(event)=>{changeHandler(event,setDay)}}>
                        <option value={0}>Saturday</option>
                        <option value={1}>Sunday</option>
                        <option value={2}>Monday</option>
                        <option value={3}>Tuesday</option>
                        <option value={4}>Wednesday</option>
                        <option value={5}>Thursday</option>
                        <option value={6}>Friday</option>
                    </select>
                </div>
                <div>
                    <p>Color</p>
                    <input type="color" id="editEventFormColor" value={color} onChange={(event)=>{changeHandler(event,setColor)}}/>
                </div>
                <div className="timeBox">
                    <p>Start time</p>
                    <input type="number" id="editEventFormStartHour" min={8} max={23} maxLength={2} value={startHour} onChange={(event)=>{changeHandler(event,setStarthour)}}/><span>:</span><input type="number" id="editEventFormStartMin" min={0} max={59} value={startMin} onChange={(event)=>{changeHandler(event,setStartmin)}}/>
                </div>
                <div className="timeBox">
                    <p>End time</p>
                    <input type="number" id="editEventFormEndHour" min={8} max={23} maxLength={2} value={endHour} onChange={(event)=>{changeHandler(event,setEndhour)}}/><span>:</span><input type="number" id="editEventFormEndMin" min={0} max={59} value={endMin} onChange={(event)=>{changeHandler(event,setEndmin)}}/>
                </div>
                </div>
                <div>
                <button id="editEventFormCancel" type="reset" onClick={()=>{setAction(false);setTrigger(prevState => prevState+1);}}>Cancel</button>
                {(action==="edit") ? <button id="editEventFormRemove" type="reset" onClick={removeEvent}>Remove</button> : null}
                <button id="editEventFormSave" type="submit">{(action==="edit") ? "Save" : "Add"}</button>
                </div>
            </div>
        </form>
    </div>)
}
export default EditEventForm