import React from "react";
import {EventCellStyled} from "./mystyledcomponents/EventCellStyled";
import EventDescription from "./EventDescription";
function EventCell({day,startHour,startMin,endHour,endMin,name,desc,color,setEventDesc}){
    const enterHandler = (e) =>{
        setTimeout(()=>{
            if(!(desc===undefined || desc===null || desc==="")){
                let tooltip = e.target.querySelector(".eventCellDesc")
                if(tooltip===null){
                    return 0
                }
                tooltip.style.transitionDelay = "0.5s";
                tooltip.style.opacity = "100%";
            }
        },10)
    }
    const leaveHandler = (e) =>{
        setTimeout(()=>{
            if(!(desc===undefined || desc===null || desc==="")){
                let tooltip = e.target.querySelector(".eventCellDesc")
                if(tooltip===null){
                    return 0
                }
                tooltip.style.transitionDelay = "0s";
                tooltip.style.opacity = "0%";
            }
        },10)
    }
    const clickHandler = ()=>{
        setEventDesc(<EventDescription day={day} startHour={startHour} startMin={startMin} endHour={endHour} endMin={endMin} name={name} desc={(desc===undefined || desc===null || desc==="") ? "No description" : desc}/>)
    }
    //{((((endHour-startHour)*6.25 + (endMin-startMin)*6.25/60)/100*(0.85*window.innerWidth))>135) ? " - " : <br/>}
    return(<a href="#eventDescContainer"><EventCellStyled onClick={clickHandler} onMouseEnter={enterHandler} onMouseLeave={leaveHandler} className="eventCell" color={color} starthour={startHour} startmin={startMin} endhour={endHour} endmin={endMin}>
        <div className="eventCellName">{name}</div>
        <div className="eventCellTime">{startHour}:{parseInt(startMin).toLocaleString("fullwide",{minimumIntegerDigits:2})}<br/>{endHour}:{parseInt(endMin).toLocaleString("fullwide",{minimumIntegerDigits:2})}</div>
        {(desc===undefined || desc===null || desc==="") ? null : <div className="eventCellDesc">{desc}</div>}
    </EventCellStyled></a>)
}
export default EventCell