import './App.css';
import {useEffect, useState} from "react";
import {createGlobalStyle} from "styled-components";
import Calendar from "./mycomponents/Calendar";
import EditSection from "./mycomponents/EditSection";
import BackupSection from "./mycomponents/BackupSection";
const GlobalStyles = createGlobalStyle`
    body{
        background-color: aliceblue;
        height: 100%;
        width: 100%;
    }
    #sidebar{
        background-color: #f7f7f7;
        width: 15%;
        height: 100vh;
        float: right;
        border-left: 1px solid white;
        position: fixed;
        top: 0;
        right: 0;
        padding: 30px 10px 10px;
        box-sizing: border-box;
    }
    div#eventDescContainer{
        width: 85%;
        padding: 20px;
        box-sizing: border-box;
    }
    div.eventDescBox{
        height: max-content;
        min-height: 100px;
        background-color: white;
        border-radius: 20px;
        box-shadow: 5px 5px 5px gray;
        padding: 15px;
    }
    .eventDescName{
        margin: 0;
        float: left;
        color: #46a8ff;
    }
    .eventDescDate{
        margin: 0;
        float: right;
        color: darkgray;
    }
    .eventDescText{
        clear:both;
        text-align: start;
        padding-top: 10px;
        word-break: break-word;
    }
    .sideBtn{
        width: 80%;
        height: 60px;
        background-color: white;
        border-radius: 10px;
        border: none;
        outline: none;
        color: #46a8ff;
        font-size: larger;
        font-weight: 600;
        box-shadow: 2px 2px 2px #b9deff;
        margin-bottom: 15px;
        user-select: none;
        cursor: pointer;
    }
    .sideBtn:hover{
        background-color: #ebebeb;
    }
    .sideBtn:active{
        color: #1892ff;
    }
    #sidebarTog{
        position: fixed;
        right: 5px;
        top: 5px;
        z-index: 1000;
        width: 50px;
        height: 50px;
        color: #1892ff;
        background-color: white;
        border: none;
        outline: none;
        border-radius: 10px;
        box-shadow: 0 0 5px #b9deff;
        display: none;
    }
    @media only screen and (max-width: 965px){
        div#eventDescContainer{
            width: 100%;
        }
        .closedSideBar{
            right: -100%!important;
        }
        .openedSideBar{
            right: 0%;
        }
        #sidebarTog{
            display: inline;
        }
        #sidebar{
            position: fixed;
            top: 0;
            transition: all 0.5s ease 0s;
            width: 100vw;
            height: 100vh;
            z-index: 500;
            background-color: rgba(0,0,0,0.3);
            color: white;
        }
        #editSection{
            width: 100%!important;
        }
        #backupSection{
            width: 100%!important;
        }
    }
`
function App() {
    const [eventDesc,setEventDesc] = useState(null)
    const [pagemode,setPagemode] = useState("calendar")
    const [sidebarTrigger,setSidebarTrigger] = useState(false)
    let pagecontent = null
    switch(pagemode){
        case "calendar":
            pagecontent = <><Calendar setEventDesc={setEventDesc}/>
            <div id="eventDescContainer">
                {eventDesc}
            </div></>;
            break;
        case "edit":
            pagecontent = <><EditSection/>
            </>
            break;
        case "backup":
            pagecontent = <><BackupSection/>
            </>
            break;
        default:
            break;
    }
    if(localStorage.getItem("WeeklyProgrammerData")===null){
        localStorage.setItem("WeeklyProgrammerData","[]")
    }
    useEffect(()=>{
        if(localStorage.getItem("WeeklyProgrammerData")===null){
            localStorage.setItem("WeeklyProgrammerData","[]")
        }
    },[])
    const sidebarToggle = () =>{
        setSidebarTrigger(!sidebarTrigger)
        if(document.getElementById("sidebar").classList.contains("closedSideBar")){
            document.getElementById("sidebar").classList.remove("closedSideBar")
            document.getElementById("sidebar").classList.add("openedSideBar")

        }else{
            document.getElementById("sidebar").classList.add("closedSideBar")
            document.getElementById("sidebar").classList.remove("openedSideBar")
        }
    }
    return (
        <div className="App">
            <GlobalStyles/>
            <button id="sidebarTog" onClick={sidebarToggle}>{(sidebarTrigger) ?
                <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor"
                     className="bi bi-x-lg" viewBox="0 0 16 16">
                    <path
                        d="M2.146 2.854a.5.5 0 1 1 .708-.708L8 7.293l5.146-5.147a.5.5 0 0 1 .708.708L8.707 8l5.147 5.146a.5.5 0 0 1-.708.708L8 8.707l-5.146 5.147a.5.5 0 0 1-.708-.708L7.293 8 2.146 2.854Z"/>
                </svg> : <svg xmlns="http://www.w3.org/2000/svg" width="25" height="25" fill="currentColor"
                              className="bi bi-list" viewBox="0 0 16 16">
                    <path fillRule="evenodd"
                          d="M2.5 12a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5z"/>
                </svg>}
            </button>
            <div id="sidebar" className="closedSideBar">
                <button className="sideBtn" onClick={()=>{setPagemode("calendar");sidebarToggle()}}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor"
                         className="bi bi-calendar4-week" viewBox="0 0 16 16">
                        <path
                            d="M3.5 0a.5.5 0 0 1 .5.5V1h8V.5a.5.5 0 0 1 1 0V1h1a2 2 0 0 1 2 2v11a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2V3a2 2 0 0 1 2-2h1V.5a.5.5 0 0 1 .5-.5zM2 2a1 1 0 0 0-1 1v1h14V3a1 1 0 0 0-1-1H2zm13 3H1v9a1 1 0 0 0 1 1h12a1 1 0 0 0 1-1V5z"/>
                        <path
                            d="M11 7.5a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-1a.5.5 0 0 1-.5-.5v-1zm-3 0a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-1a.5.5 0 0 1-.5-.5v-1zm-2 3a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-1a.5.5 0 0 1-.5-.5v-1zm-3 0a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-1a.5.5 0 0 1-.5-.5v-1z"/>
                    </svg> Calendar</button>
                <button className="sideBtn" onClick={()=>{setPagemode("edit");sidebarToggle()}}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor"
                         className="bi bi-pencil-square" viewBox="0 0 16 16">
                        <path
                            d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z"/>
                        <path fillRule="evenodd"
                              d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z"/>
                    </svg> Edit</button>
                <button className="sideBtn" onClick={()=>{setPagemode("backup");sidebarToggle()}}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor"
                         className="bi bi-arrow-repeat" viewBox="0 0 16 16">
                        <path
                            d="M11.534 7h3.932a.25.25 0 0 1 .192.41l-1.966 2.36a.25.25 0 0 1-.384 0l-1.966-2.36a.25.25 0 0 1 .192-.41zm-11 2h3.932a.25.25 0 0 0 .192-.41L2.692 6.23a.25.25 0 0 0-.384 0L.342 8.59A.25.25 0 0 0 .534 9z"/>
                        <path fillRule="evenodd"
                              d="M8 3c-1.552 0-2.94.707-3.857 1.818a.5.5 0 1 1-.771-.636A6.002 6.002 0 0 1 13.917 7H12.9A5.002 5.002 0 0 0 8 3zM3.1 9a5.002 5.002 0 0 0 8.757 2.182.5.5 0 1 1 .771.636A6.002 6.002 0 0 1 2.083 9H3.1z"/>
                    </svg> Backup</button>
                <p>Made by Y.M.M</p>
            </div>
            {pagecontent}
        </div>
    );
}

export default App;
