import React, {useState} from "react";
import {createGlobalStyle} from "styled-components";
import EditEventForm from "./EditEventForm";
import EditSectionEvent from "./EditSectionEvent";
import ImportEventForm from "./ImportEventForm";
const Editsectionstyles = createGlobalStyle`
    #editSection{
        width: 85%;
        padding: 115px 15px 15px 15px;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
    }
    #editSectionHead{
        width: 60vw;
        display: flex;
        position: fixed;
        top: 15px;
        justify-content: space-between;
    }
    #editAddBtn{
        width: 29vw;
        height: 100px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: white;
        border-radius: 10px;
        color: #46a8ff;
        box-shadow: 0px 0px 5px #b9deff;
    }
    #editImportBtn{
        width: 29vw;
        height: 100px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: white;
        border-radius: 10px;
        color: #46a8ff;
        box-shadow: 0px 0px 5px #b9deff;
    }
    #editImportBtn p{
        margin: 10px 0 0 0;
        padding: 0;
    }
    #editImportBtn:hover{
        background-color: #ebebeb;
    }
    #editImportBtn:active{
        color: #1892ff;
    }
    #editAddBtn p{
        margin: 10px 0 0 0;
        padding: 0;
    }
    #editSection a{
        display: contents;
        text-decoration: none;
    }
    #editAddBtn:hover{
        background-color: #ebebeb;
    }
    #editAddBtn:active{
        color: #1892ff;
    }
    #editSection .eventDescBox{
        box-shadow: 0px 0px 5px #b9deff;
        width: 60vw;
        box-sizing: border-box;
        margin-top: 15px;
    }
    #editSection .eventExportBox{
        box-shadow: 0px 0px 5px #b9deff;
        width: 100%;
        box-sizing: border-box;
        padding: 10px;
        background-color: white;
        color: #46a8ff;
    }
    #editSection .eventExportBox p{
        margin-top :0;
    }
    #editSection .eventExportBox input{
        background-color: lightgray;
        border: 2px solid #b9deff;
        font-family: monospace;
        width: 90%;
        outline: none;
    }
    #editSection .eventExportBox input:focus{
        border: 2px solid #1892ff;
    }
    #editSection .eventExportCopyBtn{
        border: none;
        outline: none;
        color: white;
        background-color: #7dc2ff;
        border-radius: 5px;
        padding: 5px;
        margin: 5px;
        cursor: pointer;
        width: 70px;
        height: 50px;
    }
    #editSection .eventExportCopyBtn:hover{
        background-color: #46a8ff;
    }
    .eventDescBox .eventEditBtn, .eventDescBox .eventExportBtn{
        float:left;
        margin-left: 10px;
        color: #b9deff;
        cursor: pointer;
    }
    .eventDescBox .eventEditBtn:hover, .eventDescBox .eventExportBtn:hover{
        color: #509adc;
    }
`
function EditSection() {
    const [adding,setAdding] = useState(false)
    const [triggerkey,setTriggerkey] = useState(0)
    const [importing,setImporting] = useState(false)
    const addEvent = ()=>{
        setAdding(true)
    }
    const importEvent = ()=>{
        setImporting(true)
    }
    let allevents = JSON.parse(localStorage.getItem("WeeklyProgrammerData")).sort((a,b)=>{
        return (parseInt(a.day)*1440 + parseInt(a.startHour)*60 + parseInt(a.startMin)) - (parseInt(b.day)*1440 + parseInt(b.startHour)*60 + parseInt(b.startMin))
    })
    let eventKey = 0
    return(<>
        <Editsectionstyles/>
        <div id="editSection">
            <div id="editSectionHead">
                <a href="#editSection"><div id="editAddBtn" onClick={addEvent}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="45" height="45" fill="currentColor"
                         className="bi bi-calendar2-plus-fill" viewBox="0 0 16 16">
                        <path
                            d="M3.5 0a.5.5 0 0 1 .5.5V1h8V.5a.5.5 0 0 1 1 0V1h1a2 2 0 0 1 2 2v11a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2V3a2 2 0 0 1 2-2h1V.5a.5.5 0 0 1 .5-.5zM2 3.5v1c0 .276.244.5.545.5h10.91c.3 0 .545-.224.545-.5v-1c0-.276-.244-.5-.546-.5H2.545c-.3 0-.545.224-.545.5zm6.5 5a.5.5 0 0 0-1 0V10H6a.5.5 0 0 0 0 1h1.5v1.5a.5.5 0 0 0 1 0V11H10a.5.5 0 0 0 0-1H8.5V8.5z"/>
                    </svg>
                    <p>Create new Event</p>
                </div></a>
                <a href="#editSection"><div id="editImportBtn" onClick={importEvent}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="45" height="45" fill="currentColor"
                         className="bi bi-box-arrow-in-down-left" viewBox="0 0 16 16">
                        <path fillRule="evenodd"
                              d="M9.636 2.5a.5.5 0 0 0-.5-.5H2.5A1.5 1.5 0 0 0 1 3.5v10A1.5 1.5 0 0 0 2.5 15h10a1.5 1.5 0 0 0 1.5-1.5V6.864a.5.5 0 0 0-1 0V13.5a.5.5 0 0 1-.5.5h-10a.5.5 0 0 1-.5-.5v-10a.5.5 0 0 1 .5-.5h6.636a.5.5 0 0 0 .5-.5z"/>
                        <path fillRule="evenodd"
                              d="M5 10.5a.5.5 0 0 0 .5.5h5a.5.5 0 0 0 0-1H6.707l8.147-8.146a.5.5 0 0 0-.708-.708L6 9.293V5.5a.5.5 0 0 0-1 0v5z"/>
                    </svg>
                    <p>Import new Event</p>
                </div></a>
            </div>
            {(adding) ? <EditEventForm action="add" setAction={setAdding} setTrigger={setTriggerkey}/> : null}
            {(importing) ? <ImportEventForm setImporting={setImporting}/> : null}
            {allevents.map((event)=>{
                return <EditSectionEvent key={eventKey++} setTriggerkey={setTriggerkey} name={event.name} desc={event.desc} day={event.day} startHour={event.startHour} startMin={event.startMin} endHour={event.endHour} endMin={event.endMin} color={event.color}/>
            })}
        </div>
    </>)
}
export default EditSection